.Dropdown {
   display: flex;
   flex-direction: column;

   &__label {
      display: flex;
      justify-content: space-between;
      background-color: $primary-color;
      color: white;
      width: 100%;
      padding: 13px 20px;
      border-radius: 10px;
      margin-top: 20px;
      z-index: 10;
      font-size: 18px;
   }
   &__list {
      top: 30px;
      width: 100%;
      background-color: $secondary-color;
      margin-top: -20px;
      padding-top: 40px;
      padding-bottom: 20px;
      padding-left: 20px;
      padding-right: 20px;
      z-index: 2;
      border-radius: 10px;
      height: 100%;
   }
}
