.tag {
   background-color: $primary-color;
   text-align: center;
   color: white;
   min-width: 115px;
   height: 25px;
   border-radius: 10px;
   padding: 3px 10px;
   font-size: 14px;
   margin: 12px 10px 12px 0;
   white-space: nowrap;
}

.tags {
   display: flex;
   flex-wrap: wrap;
}

ul {
   margin: 0;
}
